import API from "@/services/API";
import get from "@/util/get";
import { pagination } from "@/util/constants";

export async function getUnusedCoupons({ 
  skip = skip || pagination.skip, 
  limit = limit || pagination.limit
}) {
  const url = `/api/rest/order/list/otype/unused/${limit}/${skip}`;
  const result = await API.get(url);
  return get(result, 'data', {});
}

export async function getUsedCoupons({ 
  skip = skip || pagination.skip, 
  limit = limit || pagination.limit
}) {
  const url = `/api/rest/order/list/otype/used/${limit}/${skip}`;
  const result = await API.get(url);
  return get(result, 'data', {});
}

