<template>
  <div class="account-coupons__container account-page">
    <div class="account-page__title">
      <h2>{{ $t("pageTitle") }}</h2>
    </div>
    <LoadingWrapper :loadingKeys="[LOADING_KEY_FETCHING_COUPON]">
      <div class="account-coupons__blank" v-if="!unUsedCouponsData" />
      <DataTableCoupons
        v-if="unUsedCouponsData"
        :data="unUsedCouponsData"
        v-bind="unUsedCouponsPageInfo"
        :showPagination="getUnusedCoupons"
        :onClick="handleClickUnUsedCoupons"
      />
    </LoadingWrapper>

    <div class="account-coupons__used-coupons-title">
      <span>{{ $t("usedCoupons") }}</span>
    </div>

    <LoadingWrapper :loadingKeys="[LOADING_KEY_FETCHING_COUPON]">
      <div class="account-coupons__blank" v-if="!unUsedCouponsData" />
      <DataTableCoupons
        v-if="usedCouponsData"
        :data="usedCouponsData"
        v-bind="usedCouponsPageInfo"
        :showPagination="getUsedCoupons"
        :onClick="handleClickUsedCoupons"
      />
    </LoadingWrapper>
  </div>
</template>

<i18n>
{
  "is": {
    "pageTitle": "Gjafabréf",
    "usedCoupons": "Notaðir miðar"
  },
 "en": {
    "pageTitle": "Your Coupons",
    "usedCoupons": "Used coupons"
  }
}
</i18n>

<script>
import { mapActions } from "vuex";
import get from "@/util/get";
import { MODAL_KEY_VIEW_COUPON_DETAIL } from "@/store/modalModule";
import { LOADING_KEY_FETCHING_COUPON } from "@/store/loadingModule";
import LoadingWrapper from "@/components/LoadingWrapper";
import DataTableCoupons from "@/components/AccountDetail/DataTableCoupons";
import { getUnusedCoupons, getUsedCoupons } from "@/services/coupon";

export default {
  name: "ViewMyAccountCoupons",
  components: { DataTableCoupons, LoadingWrapper },
  data() {
    return {
      currentPage: 1,
      showEntriesUnused: undefined,
      showEntriesUsed: undefined,
      LOADING_KEY_FETCHING_COUPON,
      unUsedCoupons: {},
      usedCoupons: {},
    };
  },
  computed: {
    unUsedCouponsPageInfo() {
      return {
        totalItems: this.unUsedCouponsCount,
        showEntries: this.showEntriesUnused || 10,
      };
    },
    usedCouponsPageInfo() {
      return {
        totalItems: this.usedCouponsCount,
        showEntries: this.showEntriesUnused || 10,
      };
    },
    unUsedCouponsCount() {
      return this.unUsedCoupons.totalCount || 0;
    },
    usedCouponsCount() {
      return this.usedCoupons.totalCount || 0;
    },
    unUsedCouponsData() {
      return this.unUsedCoupons.data;
    },
    usedCouponsData() {
      return this.usedCoupons.data;
    },
  },
  methods: {
    ...mapActions(["toggleModal", "setPrintData", "toggleLoading"]),
    transformCouponsRawToData(records) {
      const data = Object.keys(records[0]).map(recordId => {
        const couponId = Object.keys(records[0][recordId])[0];
        return {
          recordId,
          couponId,
          ...records[0][recordId][couponId],
        };
      });

      return data;
    },
    handleLoading(state = true) {
      const { toggleLoading } = this;
      return toggleLoading({
        loadingKey: LOADING_KEY_FETCHING_COUPON,
        isLoading: state,
      });
    },
    handleClickUnUsedCoupons(recordId) {
      const deal = this.unUsedCouponsData.find(
        deal => deal.recordId === recordId
      );
      return this.openModal(deal);
    },
    handleClickUsedCoupons(recordId) {
      const deal = this.usedCouponsData.find(
        deal => deal.recordId === recordId
      );
      return this.openModal(deal);
    },
    openModal(payload) {
      this.setPrintData(payload);
      return this.toggleModal({
        modalKey: MODAL_KEY_VIEW_COUPON_DETAIL,
        ...payload,
      });
    },
    async loadUnusedCoupons({ skip, limit }) {
      const { records, total_count } = await getUnusedCoupons({ skip, limit });
      const data = this.transformCouponsRawToData(records);
      this.unUsedCoupons = { data, totalCount: total_count[0] };
    },
    async loadUsedCoupons({ skip, limit }) {
      const { records, total_count } = await getUsedCoupons({ skip, limit });
      const data = this.transformCouponsRawToData(records);
      this.usedCoupons = { data, totalCount: total_count[0] };
    },
    getUsedCoupons(skip, limit) {
      this.showEntriesUsed = limit;
      this.handleLoading();
      this.loadUsedCoupons({ skip, limit });
    },
    getUnusedCoupons(skip, limit) {
      this.showEntriesUnused = limit;
      this.handleLoading();
      this.loadUnusedCoupons({ skip, limit });
    },
  },
  mounted() {
    const skip = Number(get(this.$route, "query.page", 1));
    this.getUsedCoupons(skip || 1, 20);
    this.getUnusedCoupons(skip || 1, 20);
  },
  beforeUpdate() {
    const skip = Number(get(this.$route, "query.page", 1));
    if (skip !== this.currentPage) {
      this.currentPage = skip;
      this.getUsedCoupons(skip, this.showEntriesUsed);
      this.getUnusedCoupons(skip, this.showEntriesUnused);
    }
  },
  watch: {
    usedCouponsData() {
      this.handleLoading(false);
    },
    unUsedCouponsData() {
      this.handleLoading(false);
    },
  },
};
</script>

